.lepeDragon {
    .animate {
        -webkit-animation: animateLepe 2s linear infinite;
        -moz-animation: animateLepe 2s linear infinite;
        animation: animateLepe 2s linear infinite;
    }

    /* KEYFRAMES */

    @-webkit-keyframes animateLepe {
        0% {
            transform: rotate(-5deg);
        }

        50% {
            transform: rotate(5deg);
        }

        100% {
            transform: rotate(-5deg);
        }
    }

    @-moz-keyframes animateLepe {
        0% {
            transform: rotate(-5deg);
        }

        50% {
            transform: rotate(5deg);
        }

        100% {
            transform: rotate(-5deg);
        }
    }

    @keyframes animateLepe {
        0% {
            transform: rotate(-5deg);
        }

        50% {
            transform: rotate(5deg);
        }

        100% {
            transform: rotate(-5deg);
        }
    }
}